body {
    background-color: black;
    color: white;
    margin: 8px 15px;
    font-family: Arial, Helvetica, sans-serif;
}

* {
    box-sizing: border-box;
}

.Header {
    display: grid;
    align-items: center;
    grid-template-columns: 0.5fr 3fr 2fr;
}

.active {
    text-decoration: none;
    color: #ffffffad;
    margin: 15px;
    font-size: 15px;
}

.UserData {
    justify-self: end;
}

.SearchBtn,
.LoginBtn {
    background-color: black;
    color: #ffffffad;
    font-size: 15px;
    margin-right: 15px;
}

.main {
    margin: 10px 30px;
}

.displayData {
    display: flex;
    max-width: 100%;
    overflow: scroll;
    gap: 10px;
}

.activeBrick {
    width: 150px;
    overflow: visible;
    padding: 2px;
}

.displayData::-webkit-scrollbar {
    display: none;

}

.MainContainer {
    position: relative;

}

.InfoContainer {
    width: 50%;
    right: 25%;
    max-width: 100%;
    height: 65vh;
    background-color: grey;
    position: absolute;


}